import * as React from 'react';
import { COLORS } from '@src/configs';

import { Col, HomeContainer, Row } from './home.styled';
import { useAppContext } from '../../context/app.context';

type IHomeProps = {};
const Home = (_: IHomeProps) => {
  const [state] = useAppContext();
  return (
    <HomeContainer>
      {state.tickets.map((item: any) => {
        return (
          <Row>
            {item.map((_item: any) => {
              return <Col color={COLORS[_item.type]} />;
            })}
          </Row>
        );
      })}
    </HomeContainer>
  );
};
export default Home;
